.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #162a44;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 3px;
  background-color: #eeeeee;
  -webkit-transition: .4s;
  transition: .4s;
}

input[name=display0]:checked + .slider {
  background-color: #ff4655;
}
input[name=display1]:checked + .slider {
  background-color: #ffd23c;
}
input[name=display2]:checked + .slider {
  background-color: #3bff6f;
}
input[name=display3]:checked + .slider {
  background-color: #3bbaff;
}
input:checked + .slider:before {
  background-color: #001326;
}

input:focus + .slider {
  box-shadow: 0 0 1px #162a44;
}

input:checked + .slider:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

