  right: 0;
  margin: 0 auto;
  color: #eee;
  font-size: 25px;
}

/* Login/Register/Logout Pages */

.LoginPageContainer {
  height: 100%;
}
.LoginPage {
  height: 100%;
}
.LoginBody {
  width: 1200px;
	height: 100%;
	margin-right: auto;
	margin-left: auto;
	display:block;
	position: relative;
}
.LoginForm {
  width: 400px;
  height: 400px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.RegisterForm {
  height: 500px !important;
}
.LoginForm h1 {
  text-align: center;
  font-size: 50px;
  line-height: 60px;
  margin: 15px 0;
}
.LoginForm label {
  width: 100%;
  display: block;
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: #ff4655;
  padding: 15px 0 10px 0;
}
.LoginForm input {
  width: 80%;
  height: 50px;
  position: relative;
  display: block;
  left: 0;
  right: 0;
  margin: 0 auto;
  margin-top: 40px;
  background-color: #10163c;
  color: #eee;
  border: none;
  border-bottom: 2px solid #364961;
  text-align: center;
  font-size: 18px;
}
.LoginForm input:hover {
  border-bottom: 2px solid #ff4655;
}
.LoginForm input:focus {
  border-bottom: 2px solid #ff4655;
}
.LoginForm input[type=submit] {
  width: 80%;
  height: 50px;
  position: absolute;
  display: block;
  left: 0;
  right: 0;
  bottom: 30px;
  margin: 0 auto;
  background-color: #364961;
  color: #001326;
  border: 2px solid #364961;
  text-align: center;
  font-size: 18px;
}
.LogOutBody {
  width: 1200px;
	height: 100%;
	margin-right: auto;
	margin-left: auto;
	display:block;
	position: relative;
}
.LogOutForm {
  width: 300px;
  height: 150px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.LogOutForm h1 {
  text-align: center;
  font-size: 50px;
  line-height: 60px;
  margin: 15px 0;
}

@media screen and (max-width: 1300px) {

  /* Index Page */

  .ParallaxCharacters {
    width: 540px;
    top: 200px;
  }
  .TitleContainer {
    width: 400px;
  }
  .TitleLine1 {
    font-size: 60px;
    line-height: 80px;
  }
  .TitleLine3 {
    font-size: 80px;
    line-height: 100px;
    letter-spacing: 10px;
  }
  .TitleDesc {
    font-size: 20px;
    text-align: center;
  }
  .ActionButton {
    margin: 15px auto; 
  }
}

@media screen and (max-width: 1000px) {

  /* Index Page */

  .ParallaxCharacters {
    width: 450px;
    top: 420px;
    right: 50%;
    transform: translateX(50%);
  }
  .TitleContainer {
    width: 337px;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
  }
  .TitleLine1 {
    font-size: 50px;
    line-height: 60px;
  }
  .TitleLine3 {
    font-size: 70px;
    line-height: 80px;
    letter-spacing: 7.5px;
  }
  .TitleDesc {
    font-size: 18px;
  }
  .IndexSearchContainer form input[type=text] {
    width: 70%;
  }
  .IndexSearchContainer form input[type=submit] {
    width: 30%;
  }
  .SectionContainer {
    height: 350px;
  }
  .InfoMiniHeader {
    font-size: 16px;
  }
  .InfoDescription {
    font-size: 22px;
  }
}

@media screen and (max-width: 1200px) {
  
  /* Player Page */
  
  #navBarContainer {
    width: 900px;
  }
  .MainBody {
    width: 900px;
  }
  .ControlHeaderContainer {
    height: 70px;
  }
  .ControlHeader {
    font-size: 24px;
  }
  .ControlSubHeader {
    line-height: 20px;
    margin-top: 38px;
  }
  .ControlButtonsBox {
    height: 80px;
  }
  .WidgetLayout td p {
    font-size: 15px;
    width: 60%;
  }
  .ListButtonContainer button {
    height: 40px;
    font-size: 20px;
  }
  .RankIconContainer img {
    width: 170px;
  }
  .RankHistory svg {
    width: 600px;
  }
  .PerfStat {
    font-size: 30px;
    line-height: 45px;
  }
  .PerfStat i {
    line-height: 30px;
    font-size: 18px !important;
  }
  .PerfStatMain {
    font-size: 50px;
    line-height: 55px;
  }
  .PerfStatMain i {
    font-size: 28px;
  }
  .AllTimeStat {
    font-size: 30px;
    line-height: 45px;
  }
  .AllTimeStat i {
    line-height: 30px;
    font-size: 18px !important;
  }
  .AllTimeStatMain {
    font-size: 50px;
    line-height: 55px;
  }
  .AllTimeStatMain i {
    font-size: 28px;
  }
  .MainStat svg {
    width: 400px;
  }
  .MapContainer img {
    width: 180px;
  }
  .MinorStats {
    height: 90px;
  }
  .MinorStatValue {
    font-size: 26px;
    padding: 10px 10px 5px 10px;
  }
  .MinorStatTitle {
    font-size: 15px;
    padding: 5px 10px 10px 10px;
  }
  .CumulativeStats svg {
    width: 800px
  }
}



